                    <cart-content :editable="false" :connectObj="thisOrder.cart"></cart-content>
                  </div>
                </div>
              </div>
              <div class="showInfo">
                <div class="showInfo__block">
                  <div class="showInfo__title">
                    結帳金額
                  </div>
                  <div class="showInfo__body">
                    <ul class="showInfo__list">
                      <li class="showInfo__item">
                        <p class="showInfo__key">小計</p>
                        <div class="showInfo__value">
                          <p>NT$ {{thisOrder.cart.sum}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">使用優惠券</p>
                        <div class="showInfo__value">
                          <p>{{thisOrder.coupon.id}}</p>
