              </div>
              <div class="showInfo">
                <div class="showInfo__block">
                  <div class="showInfo__title">
                    發票
                  </div>
                  <div class="showInfo__body">
                    <ul class="showInfo__list">
                      <li class="showInfo__item">
                        <p class="showInfo__key">發票類型</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.type}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '個人電子發票'">
                        <p class="showInfo__key">載具類型</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.carrier}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '個人電子發票'
                      && convertedInvoiceInfo.carrier === '不使用載具'">
                        <p class="showInfo__key">中獎發票寄送地址</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.address}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '個人電子發票'
                      && convertedInvoiceInfo.carrier === '手機條碼'">
                        <p class="showInfo__key">手機條碼</p>
