                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '個人電子發票'
                      && convertedInvoiceInfo.carrier === '自然人憑證'">
                        <p class="showInfo__key">自然人憑證</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.natural_person_code}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '捐贈電子發票'">
                        <p class="showInfo__key">捐贈單位</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.donateOrg}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '公司報帳電子發票'">
                        <p class="showInfo__key">買受人</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.companyTitle}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="convertedInvoiceInfo.type === '公司報帳電子發票'">
                        <p class="showInfo__key">統一編號</p>
                        <div class="showInfo__value">
                          <p>{{convertedInvoiceInfo.companyTaxId}}</p>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="showInfo">
                <div class="showInfo__block">
                  <div class="showInfo__title">
                    配送資訊
                  </div>
                  <div class="showInfo__body">
                    <ul class="showInfo__list">
                      <li class="showInfo__item">
                        <p class="showInfo__key">是否門市取貨</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.get_from_store}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="!thisOrder.delivery.get_from_store">
                        <p class="showInfo__key">收貨人姓名</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.name}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="!thisOrder.delivery.get_from_store">
                        <p class="showInfo__key">收貨人聯絡電話</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.tel}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="!thisOrder.delivery.get_from_store">
                        <p class="showInfo__key">配送地址</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.address}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">收件地址是否有櫃檯或管理員可以代收?</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.has_recipient2}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item"
                      v-if="thisOrder.delivery.has_recipient2">
                        <p class="showInfo__key">代收公司 / 大樓或社區名稱</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.recipient2Name}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">匿名配送</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.anonymous}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">希望到貨日期</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.esdDate}}</p>
                        </div>
                      </li>

                      <li class="showInfo__item">
                        <p class="showInfo__key">希望到貨時間</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.esdTime}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">卡片內容</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.cardContent}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">備註</p>
                        <div class="showInfo__value">
                          <p>{{convertedDeliveryInfo.remark}}</p>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- payment info -->
      <transition name="fade">
        <div class="checkout__block">
          <p
            class="checkout__title checkout__title--collapse"
            :class="paymentInfoCollapse ? null : 'collapsed'"
            :aria-expanded="paymentInfoCollapse ? 'true' : 'false'"
            aria-controls="collapse__orderInfo"
            @click.prevent="paymentInfoCollapse = !paymentInfoCollapse"
          >
            <span> 付款方式 </span>
          </p>
          <b-collapse id="collapse__orderInfo" v-model="paymentInfoCollapse">
            <div class="checkout__body">
              <div class="showInfo" v-if="thisOrder.payment.status === 'notYet'">
                <div class="showInfo__block">
                  <div class="showInfo__title">
                    信用卡付款
                  </div>
                  <div class="showInfo__body">
                    <div class="payment">
                      <p class="payment__sum">
                        總應付金額: NT${{thisOrder.sum}}
                      </p>
                      <form @submit.prevent="go_pay">
                        <div class="myForm">
                          <span class="myForm__title">信用卡號</span>
                          <input type="text" required v-model="payment.creditNum">
                        </div>
                        <div class="myForm">
                          <span class="myForm__title">信用卡有效月 / 年</span>
                          <div class="myForm__inputRow">
                            <input type="text" placeholder="月" required
                            v-model="payment.creditExp.m">
                            <input type="text" placeholder="年" required
                            v-model="payment.creditExp.y">
                          </div>
                        </div>
                        <div class="myForm">
                          <span class="myForm__title">背面末三碼</span>
                          <input type="text" required v-model="payment.cvc">
                        </div>
                        <div class="myForm">
                          <div class="myForm__submit">
                            <button type="submit">
                              確認付款
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
              <div class="showInfo" v-if="thisOrder.payment.status !== 'notYet'">
                <div class="showInfo__block">
                  <div class="showInfo__title">
                    付款資訊
                  </div>
                  <div class="showInfo__body">
                    <ul class="showInfo__list">
                      <li class="showInfo__item">
                        <p class="showInfo__key">已付款時間</p>
                        <div class="showInfo__value">
                          <p>{{thisOrder.payment.time}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">付款方式</p>
                        <div class="showInfo__value">
                          <p>{{thisOrder.payment.type}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">信用卡號</p>
                        <div class="showInfo__value">
                          <p>{{thisOrder.payment.creditNum}}</p>
                        </div>
                      </li>
                      <li class="showInfo__item">
                        <p class="showInfo__key">結帳金額</p>
                        <div class="showInfo__value">
                          <p>NT${{thisOrder.sum}}</p>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </b-collapse>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import CartContent from '../../components/CartContent.vue';

export default {
  components: { CartContent },
  computed: {
    orderId() {
      return this.$route.params.orderId;
    },
    orders() {
      let orders = [];
      this.$bus.$on('son_get_data', (data) => {
        orders = data.orders;
      });
      this.$bus.$emit('son_call_data');
      return orders;
    },
    thisOrder() {
      return this.orders.find((item) => item.id === this.orderId);
    },
  },
  data() {
    return {
      orderInfoCollapse: true,
      paymentInfoCollapse: true,
      convertedInvoiceInfo: {
        type: '',
      },
      convertedDeliveryInfo: {
        get_from_store: false,
        name: '',
        tel: '',
        address: {},
        has_recipient2: true,
        recipient2Name: '',
        anonymous: false,
        esdDate: '',
        esdTime: '',
        cardContent: '',
        remark: '',
      },
      payment: {
        time: '',
        status: '',
        type: 'creditCard',
        creditNum: '',
        creditExp: {
          m: '',
          y: '',
        },
        cvc: '',
      },
    };
  },
  methods: {
    convert_date(theDate) {
      const date = theDate.split('');
      const y = date.splice(0, 4).join('');
      const m = date.splice(0, 2).join('');
      const d = date.splice(0, 2).join('');
      return `${y}-${m}-${d}`;
    },
    convert_invoiceInfo() {
      const invoiceT = this.thisOrder.invoice.type;
      switch (invoiceT) {
        case 'personal':
          this.convertedInvoiceInfo.type = '個人電子發票';
          this.$set(this.convertedInvoiceInfo, 'carrier', this.thisOrder.invoice.carrier);
          this.convert_carrierInfo();
          break;
        case 'donate':
          this.convertedInvoiceInfo.type = '捐贈電子發票';
          this.$set(this.convertedInvoiceInfo, 'donateOrg', this.thisOrder.invoice.org);
          break;
        case 'company':
          this.convertedInvoiceInfo.type = '公司報帳電子發票';
          this.$set(this.convertedInvoiceInfo, 'companyTitle', this.thisOrder.invoice.companyTitle);
          this.$set(this.convertedInvoiceInfo, 'companyTaxId', this.thisOrder.invoice.companyTaxId);
          break;
        default:
      }
    },
    convert_carrierInfo() {
      const carrierT = this.convertedInvoiceInfo.carrier;
      switch (carrierT) {
        case 'no':
          this.convertedInvoiceInfo.carrier = '不使用載具';
          this.$set(this.convertedInvoiceInfo, 'address', this.thisOrder.invoice.address);
          this.convertedInvoiceInfo.address = this
            .convert_addressString(this.convertedInvoiceInfo.address);
          break;
        case 'mobile':
          this.convertedInvoiceInfo.carrier = '手機條碼';
          this.$set(this.convertedInvoiceInfo, 'mobile_carrier', this.thisOrder.invoice.mobile_carrier);
          break;
        case 'natural_person':
          this.convertedInvoiceInfo.carrier = '自然人憑證';
          this.$set(this.convertedInvoiceInfo, 'natural_person_code', this.thisOrder.invoice.natural_person_code);
          break;
        default:
      }
    },
    convert_addressString(obj) {
      const c = obj.county;
      const d = obj.district;
      const z = obj.zipCode;
      const D = obj.detail;
      return `${z} ${c}${d}${D}`;
    },
    convert_deliveryInfo() {
      const obj = this.convertedDeliveryInfo;
      obj.get_from_store = this.thisOrder.delivery.get_from_store ? '是' : '否';
      obj.name = this.thisOrder.recipient.name;
      obj.tel = this.thisOrder.recipient.tel;
      obj.address = this.convert_addressString(this.thisOrder.recipient.address);
      obj.has_recipient2 = this.thisOrder.delivery.has_recipient2 ? '是' : '否';
      obj.recipient2Name = this.thisOrder.delivery.recipient2Name;
      obj.anonymous = this.thisOrder.delivery.anonymous ? '是' : '否';
      obj.esdDate = this.thisOrder.delivery.esdDate;
      obj.esdTime = this.convert_esdTime(this.thisOrder.delivery.esdTime);
      obj.cardContent = this.thisOrder.others.cardContent;
      obj.remark = this.thisOrder.others.remark;
    },
    /* eslint-disable */
    convert_esdTime(esdTime) {

      switch (esdTime) {
        case '':
          return '不指定';
          break;
        case 'morning':
          return '上午( 08:00 ~ 13:00 )';
          break;
        case 'afternoon':
          return '下午( 14:00 ~ 18:00 )';
          break;
        default:
      }
    },
    /* eslint-enable */
    go_pay() {
      this.payment.time = (new Date()).timeText();
      this.$bus.$emit('make_payment', this.thisOrder.id, this.payment);
      alert('付款完成');
    },
  },
  created() {
    this.convert_invoiceInfo();
    this.convert_deliveryInfo();
  },
};
</script>
